import { Box, Divider, Grid, Typography, } from '@mui/material'
import * as Yup from 'yup';
import { Form, Formik } from 'formik'
import React, { useEffect } from 'react'
import { InputText } from '../../components/inputs/InputText'
import GroupIcon from '@mui/icons-material/Group';
import { CircularButton } from '../../components/buttons/CircularButton';
import SpeedDialIcon from '@mui/material/SpeedDialIcon';
import CloseIcon from '@mui/icons-material/Close';
import { useDispatch, useSelector } from 'react-redux';
import { formCancel, formFetched, formInit, formNew } from '../../redux/actions/form.actions';
import { SearchModal } from '../../components/QuickSearch/SearchModal';
import { uiCleanQuickSearch, uiShowQuickSearch } from '../../redux/actions/ui.actions';
import { theme } from '../../configtheme';
import { SaveOutlined } from '@mui/icons-material';
import SearchIcon from '@mui/icons-material/Search';
import DeleteIcon from '@mui/icons-material/Delete';
import { Loading } from '../../components/Loadings/Loading';
import ErrorAlert from '../../components/Alerts/ErrorAlert';
import { CreateCliente, DeleteCliente, UpdateCliente } from '../../redux/actions/cliente.actions';
import { ClientesColumn } from '../../components/QuickSearch/QsColumns';
import { useState } from 'react';
import { useRef } from 'react';

const fabStyle = {
    // position: 'fixed',
    color: 'white',
    backgroundColor: `${theme.palette.valen.secondary}`,
    fontSize: '24px',
};
const fabStyle2 = {
    // position: 'fixed',
    color: 'white',
    backgroundColor: `${theme.palette.valen.error}`,
    fontSize: '24px',
};


const initialValues = {
    cedula: '',
    nombre: '',
    apellido: '',
    telefono: '',
    direccion: '',
    email: '',
    empresa: '',
    direccion_empresa: '',
    observacion: '',
}

const validation = Yup.object({
    cedula: Yup.string().max(15, 'La cedula debe contener 15 caracteres o menos').required('La cedula es requerida'),
    nombre: Yup.string().min(3, 'Debe tener al menos 3 caracteres').required('El nombre debe ser requerido'),
    apellido: Yup.string().required('El apellido debe ser requerido'),
    telefono: Yup.string().required('El telefono debe ser requerido'),
    direccion: Yup.string().required('La direccion debe ser requerida'),
    email: Yup.string(),
    empresa: Yup.string(),
    direccion_empresa: Yup.string(),
})

const Cliente = () => {
    const ref = useRef(null);
    const dispatch = useDispatch();
    const { cancel, newE, disabled, save, canDelete } = useSelector(state => state.form);
    const [formValues, setFormValues] = useState(null)
    const { quickSearchSelected } = useSelector(state => state.ui);

    useEffect(() => {
        if (quickSearchSelected) {
            return loadData();
        }
        setFormValues(null);
    }, [quickSearchSelected])

    useEffect(() => {
        dispatch(formInit());
        dispatch(uiCleanQuickSearch());
    }, [dispatch])

    const handleNew = (e) => {
        dispatch(formNew());
        dispatch(uiCleanQuickSearch());
        setTimeout(() => {
            ref.current.focus();
        }, 300)

    }
    const handleCancel = (e) => {
        dispatch(formCancel());
        dispatch(uiCleanQuickSearch());
    }

    const handleSubmit = (data) => {
        if (data._id) {
            return dispatch(UpdateCliente(data))
        }
        dispatch(CreateCliente(data))
    };

    const handleSearch = () => {
        dispatch(uiShowQuickSearch());
    }

    const handleDelete = () => {
        dispatch(DeleteCliente(quickSearchSelected._id))
        dispatch(uiCleanQuickSearch());
    }

    const loadData = () => {
        setFormValues(quickSearchSelected);
        dispatch(formFetched());
    };
    return (
        <>
            <Formik
                initialValues={formValues || initialValues}
                onSubmit={(values) => {
                    // console.log(values);
                    handleSubmit(values);
                }}
                validationSchema={validation}
                enableReinitialize
            >
                {
                    ({ handleReset, isValid, errors, resetForm }) => (
                        <Form>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                <Typography variant="h6" gutterBottom>
                                    Cliente
                                </Typography>
                                {newE && (
                                    <>
                                        <CircularButton type="reset" text="Agregar" Icon={SpeedDialIcon} handleClick={() => { handleNew(); handleReset(); }} />
                                        <CircularButton type="button" text="Buscar" Icon={SearchIcon} handleClick={handleSearch} />
                                    </>
                                )}
                                {cancel && <CircularButton type="reset" text="Cancelar" Icon={CloseIcon} handleClick={() => { handleCancel(); handleReset(); }} />}
                                <GroupIcon />
                            </div>
                            <Divider />
                            <Grid container spacing={1}>
                                <Grid item xs={12} md={3}>
                                    <InputText
                                        autoFocus
                                        ref={ref}
                                        disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        id="cedula"
                                        label="Cedula"
                                        name="cedula"
                                    />
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <InputText
                                        disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        id="nombre"
                                        label="Nombres"
                                        name="nombre"
                                    />
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <InputText
                                        disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        id="apellido"
                                        label="Apellidos"
                                        name="apellido"
                                    />
                                </Grid>
                                <Grid item xs={12} md={3}>
                                    <InputText
                                        disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        id="telefono"
                                        label="Telefono"
                                        name="telefono"
                                    />
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <InputText
                                        disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        id="direccion"
                                        label="Direccion"
                                        name="direccion"
                                    />
                                </Grid>
                                <Grid item xs={12} md={5}>
                                    <InputText
                                        disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        id="email"
                                        label="Email"
                                        name="email"
                                    />
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <InputText
                                        disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        id="empresa"
                                        label="Empresa"
                                        name="empresa"
                                    />
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <InputText
                                        disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        id="direccion_empresa"
                                        label="Direccion Empresa"
                                        name="direccion_empresa"
                                    />
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <InputText
                                        disabled={disabled}
                                        margin="dense"
                                        fullWidth
                                        id="observacion"
                                        label="Observacion"
                                        name="observacion"
                                    />
                                </Grid>
                            </Grid>

                            <Box>
                                <div style={{ display: 'flex', justifyContent: 'space-around', marginTop: '10px' }}>
                                    {
                                        canDelete &&
                                        <CircularButton size="large" style={fabStyle2} type="button" text="Eliminar" Icon={DeleteIcon} handleClick={() => { handleDelete(); handleReset(); }} />
                                    }
                                    {
                                        save &&
                                        <CircularButton size="large" style={fabStyle} type="submit" text="Guardar Cambios" Icon={SaveOutlined} />
                                    }
                                </div>


                            </Box>
                            {/* <FloatButton handleSave={handleSave} handleSearch={handleSearch} handleDelete={handleDelete} /> */}
                        </Form>
                    )
                }
            </Formik>
            <Loading />
            <ErrorAlert />
            <SearchModal title="busqueda de clientes" api="clientes/getCliente" columns={ClientesColumn} />
        </>
    )
}

export default Cliente